    <div class="container">
      <div class="checkout-order">
        <!-- process step -->
        <div class="check-step">
          <ul>
            <li class="cur"><span>Confirm</span> address</li>
            <li class="cur"><span>View your</span> order</li>
            <li><span>Make</span> payment</li>
            <li><span>Order</span> confirmation</li>
          </ul>
        </div>

        <!-- order list -->
        <div class="page-title-normal checkout-title">
          <h2><span>Order content</span></h2>
        </div>
        <div class="item-list-wrap confirm-item-list-wrap">
          <div class="cart-item order-item">
            <div class="cart-item-head">
              <ul>
                <li>Order contents</li>
                <li>Price</li>
                <li>Quantity</li>
                <li>Subtotal</li>
              </ul>
            </div>
            <ul class="cart-item-list">
              <li v-for="(val, index) in cartList" :key="index">
                <div class="cart-tab-1">
                  <div class="cart-item-pic">
                    <img src="../../static/1.jpg" :alt="val.productName" />
                  </div>
                  <div class="cart-item-title">
                    <div class="item-name">{{ val.productName }}</div>
                  </div>
                </div>
                <div class="cart-tab-2">
                  <div class="item-price">{{ val.scalePrice }}</div>
                </div>
                <div class="cart-tab-3">
                  <div class="item-quantity">
                    <div class="select-self">
                      <div class="select-self-area">
                        <span class="select-ipt">×{{ val.productNum }}</span>
                      </div>
                    </div>
                    <div class="item-stock item-stock-no">In Stock</div>
                  </div>
                </div>
                <div class="cart-tab-4">
                  <div class="item-price-total">
                    {{ val.productNum * val.scalePrice }}
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>

        <!-- Price count -->
        <div class="price-count-wrap">
          <div class="price-count">
            <ul>
              <li>
                <span>Item subtotal:</span>
                <span>{{ subtotal }}</span>
              </li>
              <li>
                <span>Shipping:</span>
                <span>{{ shipping }}</span>
              </li>
              <li>
                <span>Discount:</span>
                <span>{{ discount }}</span>
              </li>
              <li>
                <span>Tax:</span>
                <span>{{ tax }}</span>
              </li>
              <li class="order-total-price">
                <span>Order total:</span>
                <span>{{ orderTotal }}</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="order-foot-wrap">
          <div class="prev-btn-wrap">
            <router-link to="/address" class="btn btn--m">Previous</router-link>
          </div>
          <div class="next-btn-wrap">
            <button class="btn btn--m btn--red" @click="payMent">
              Proceed to payment
            </button>
          </div>
        </div>
      </div>
    </div>
    <shop-footer></shop-footer>
  </div>
</template>
<script>
import ShopHeader from "@/components/ShopHeader";
import ShopFooter from "@/components/ShopFooter";
import ShopBread from "@/components/ShopBread";
import Modal from "@/components/modal";
import axios from "axios";
export default {
  components: {
    ShopHeader,
    ShopFooter,
    ShopBread,
    Modal,
  },
  data() {
    return {
      addressId: "",
      cartList: [],
      subtotal: 0, /// 购物车总金额
      shipping: 10, // 派送费
      discount: 10, // 折扣
      tax: 0, // 税
      orderTotal: 0, // 算完后的总金额
    };
  },
  mounted() {
    this.addressId = this.$route.query.addressId;
    this.getCartList();
  },
  methods: {
    payMent() {
      axios
        .post("/users/payMent", {
          addressId: this.addressId,
          orderTotal: this.orderTotal,
        })
        .then((res) => {
          // console.log(res);
          if (res.data.status == 0) {
            this.$router.push({
              path: "/orderSuccess?",
              query: {
                orderId: res.data.result.orderId,
                orderTotal: res.data.result.orderTotal,
              },
            });
          }
        });
    },
    getCartList() {
      axios.get("/users/cartList").then((res) => {
        // console.log(res);
        if (res.data.status == 0) {
          this.cartList = res.data.result.filter((val) => val.checked);
          this.subtotal = this.cartList.reduce(
            (a, val) => val.productNum * val.scalePrice + a,
            0
          );
        }
        this.orderTotal =
          this.subtotal + this.shipping + this.tax - this.discount;

        // console.log(this.cartList);
      });
    },
  },
};
</script>
